'use client';

import { useEffect, useState } from 'react';
import { FaBars, FaTimes, FaChevronDown } from 'react-icons/fa';
import Image from 'next/image';
import Link from 'next/link';
import { motion, AnimatePresence } from 'framer-motion';

export default function Navbar() {
  const [activeSection, setActiveSection] = useState<string | null>(null);
  const [menuOpen, setMenuOpen] = useState(false);
  const [language, setLanguage] = useState('English');
  const [openMenu, setOpenMenu] = useState<string | null>(null);

  useEffect(() => {
    document.documentElement.style.scrollBehavior = 'smooth';
    return () => {
      document.documentElement.style.scrollBehavior = 'auto';
    };
  }, []);

  useEffect(() => {
    const observer = new IntersectionObserver(
      (entries) => {
        entries.forEach((entry) => {
          if (entry.isIntersecting) {
            setActiveSection(entry.target.id);
          }
        });
      },
      { threshold: 0.4 }
    );

    const ids = ['home', 'about', 'service', 'web', 'marketing', 'contact'];
    ids.forEach((id) => {
      const section = document.getElementById(id);
      if (section) observer.observe(section);
    });

    return () => observer.disconnect();
  }, []);

  const toggleMenu = (menu: string) => {
    setOpenMenu((prev) => (prev === menu ? null : menu));
  };

  return (
    <motion.header
      initial={{ y: -80, opacity: 0 }}
      animate={{ y: 0, opacity: 1 }}
      transition={{ duration: 0.5, ease: 'easeOut' }}
      className="fixed top-0 left-0 right-0 z-50 bg-gradient-to-r from-black via-red-950 to-black bg-opacity-90 transition-all duration-300 font-playfair border-2 m-3 md:rounded-full rounded-2xl"
    >
      <div className="flex justify-between items-center px-4 py-2 sm:px-8">
        {/* Logo */}
        <motion.div
          initial={{ scale: 0.8, opacity: 0 }}
          animate={{ scale: 1, opacity: 1 }}
          transition={{ duration: 0.5 }}
        >
          <Image
            src="/logo.png"
            alt="Logo"
            width={80}
            height={80}
            className="object-contain transition-transform duration-500 hover:scale-110"
            priority
          />
        </motion.div>

        {/* Desktop Menu */}
        <nav className="hidden sm:flex gap-6 items-center text-white">
          <a href="#home" className={navLinkClass(activeSection === 'home')}>Home</a>
          <a href="#about" className={navLinkClass(activeSection === 'about')}>About</a>

          <div className="relative">
            <button
              onClick={() => toggleMenu('services')}
              className="flex items-center space-x-1 hover:text-orange-400 transition"
            >
              <span className="uppercase text-base font-semibold">Services</span>
              <FaChevronDown size={12} />
            </button>
            {openMenu === 'services' && (
              <div className="absolute top-8 left-0 bg-white text-black shadow-md rounded-md w-48 py-2 z-50">
                <a href="#web" className="block px-4 py-2 hover:bg-gray-100">ERP Software</a>
                <a href="#marketing" className="block px-4 py-2 hover:bg-gray-100">HRM Software</a>
              </div>
            )}
          </div>

          <Link href="/product" className={navLinkClass(false)}>Product</Link>
          <a href="#contact" className={navLinkClass(activeSection === 'contact')}>Contact</a>
        </nav>

        {/* Right Tools */}
        <div className="hidden sm:flex items-center gap-4">
          <select
            aria-label="language"
            className="bg-black border rounded px-2 py-1 text-base text-white"
            value={language}
            onChange={(e) => setLanguage(e.target.value)}
          >
            <option>English</option>
            <option>தமிழ்</option>
            <option>සිංහල</option>
          </select>
          <button className="uppercase text-white font-semibold bg-orange-500 px-3 py-1 rounded-2xl hover:bg-orange-700">
            Login
          </button>
        </div>

        {/* Mobile Toggle */}
        <button
          onClick={() => setMenuOpen(!menuOpen)}
          className="sm:hidden text-2xl text-white"
          aria-label="Toggle Menu"
        >
          {menuOpen ? <FaTimes /> : <FaBars />}
        </button>
      </div>

      {/* Mobile Menu */}
      <AnimatePresence>
        {menuOpen && (
          <motion.div
            initial={{ height: 0, opacity: 0 }}
            animate={{ height: 'auto', opacity: 1 }}
            exit={{ height: 0, opacity: 0 }}
            transition={{ duration: 0.3 }}
            className="sm:hidden px-4 py-4 space-y-4 text-center shadow-md overflow-hidden bg-white text-black"
          >
            <a href="#home" onClick={() => setMenuOpen(false)} className={mobileLinkClass(activeSection === 'home')}>Home</a>
            <a href="#about" onClick={() => setMenuOpen(false)} className={mobileLinkClass(activeSection === 'about')}>About</a>

            <div className="space-y-2">
              <a href="#service" onClick={() => setMenuOpen(false)} className={mobileLinkClass(activeSection === 'service')}>Service</a>
              <div className="pl-4 p-2">
                <a href="#web" onClick={() => setMenuOpen(false)} className="block px-4 py-2 rounded-full hover:bg-orange-700 hover:text-white">Web Development</a>
                <a href="#marketing" onClick={() => setMenuOpen(false)} className="block px-4 py-2 rounded-full hover:bg-orange-700 hover:text-white">Digital Marketing</a>
              </div>
            </div>

            <Link href="/product" onClick={() => setMenuOpen(false)} className={mobileLinkClass(false)}>Product</Link>
            <a href="#contact" onClick={() => setMenuOpen(false)} className={mobileLinkClass(activeSection === 'contact')}>Contact</a>

            <div className="mt-4 space-y-2">
              <select
                aria-label="language"
                className="w-full border rounded px-2 py-1 text-sm"
                value={language}
                onChange={(e) => setLanguage(e.target.value)}
              >
                <option>English</option>
                <option>தமிழ்</option>
                <option>සිංහල</option>
              </select>
              <button className="w-full uppercase text-white font-semibold bg-orange-500 px-3 py-1 rounded-2xl hover:bg-orange-700">
                Login
              </button>
            </div>
          </motion.div>
        )}
      </AnimatePresence>
    </motion.header>
  );
}

// Helper styles
function navLinkClass(isActive: boolean) {
  return `uppercase text-base font-semibold px-3 py-1 rounded-2xl transition-all ${
    isActive
      ? 'bg-orange-500 text-white shadow-md'
      : 'hover:bg-orange-700 hover:text-white hover:shadow-md'
  }`;
}

function mobileLinkClass(isActive: boolean) {
  return `block uppercase text-base font-semibold px-3 py-1 rounded-2xl transition-all ${
    isActive
      ? 'bg-orange-500 text-white shadow-md'
      : 'hover:bg-orange-700 hover:text-white hover:shadow-md'
  }`;
}
